export default class Singer {
  /**
   * @param {String} name 
   * @param {String}} id id
   */
  constructor(name, id) {
    this.name = name
    this.id = id
  }

  /**
   * avatar()
   */
  addAvatar() {
    const avatar = `https://y.gtimg.cn/music/photo_new/T001R500x500M000${this.id}.jpg?max_age=2592000`
    return {
      name: this.name,
      id: this.id,
      avatar
    }
  }
}
