import originJsonp from 'jsonp'
import axios from 'axios'
import Util from './index'

const util = new Util()

export default class Request {
  /**
   * Promisjsonp
   * @param {String} url 
   * @param {Object} data url
   * @param {Object} options 
   * @param {String} options.param callback
   * @param {String} options.prefix params__jp
   * @param {String} options.name  __jp+
   * @param {String} options.timeout 60000mserror
   */
  jsonp(url, data, options) {
    // dataurl
    url += ((url.indexOf('?') < 0) ? '?' : '&') + util.convertsQuery(data)
    return new Promise((resolve, reject) => {
      originJsonp(url, options, (err, data) => {
        if (!err) {
          resolve(data)
        } else {
          reject(err)
        }
      })
    })
  }

  /**
   * XMLHttpRequests
   * @param {Object} arg
   * @param {String} arg.url 
   * @param {String} arg.headers ()
   * @param {String} arg.params ()
   */
  accessProxy(arg) {
    const URL = arg.url
    const data = {
      headers: arg.headers,
      params: arg.params
    }
    return axios.post(URL, data)
      .then(res => {
        return res.data
      })
  }
}
