/**
 * 
 * @class
 */
export default class Util {
  constructor() {
    this.vender = vendor()
  }

  /**
   * 
   * @param {*} el 
   * @param {*} className 
   */
  addClass(el, className) {
    if (!this.hasClass(el, className)) {
      el.className += el.className === ''
        ? className
        : ` ${className}`
    }
  }

  /**
   * 
   * @param {*} el 
   * @param {*} className 
   */
  hasClass(el, className) {
    const reg = new RegExp('(^|\\s)' + className + '(\\s|$)')
    return reg.test(el.className)
  }

  /**
   * "&key=value&key2=value2"
   * @param {*} data 
   */
  convertsQuery(data) {
    if (!(Object.prototype.toString.call(data) === '[object Object]')) {
      throw new Error('convertsQuery')
    }
    let url = ''
    let key
    let value
    for (key in data) {
      value = data[key] !== undefined
        ? data[key]
        : ''
      url += `&${key}=${encodeURIComponent(value)}`
    }
    return url ? url.substring(1) : ''
  }

  /**
   * ()
   * @param {*} el 
   * @param {String} attrName 
   * @param {*} val (gettersetter)
   */
  attr(el, attrName, val) {
    const prefix = 'data-'
    const name = prefix + attrName
    if (val) {
      return el.setAttribute(name, val)
    } else {
      return el.getAttribute(name)
    }
  }

  /**
   * 
   * @param {*} elements 
   * @return Array 
   */
  getElPosition(elements) {
    const elsLen = elements.length
    const result = [0]
    let position = 0

    for (let i = 0; i < elsLen; i++) {
      position += elements[i].clientHeight
      result.push(position)
    }
    return result
  }

  /**
   * 
   * @param {Array} elPositionSet 
   * @param {Number} currentY 
   */
  getCurrentIndex(elPositionSet, currentY) {
    const length = elPositionSet.length
    let diffHeight = 0
    let result = 0

    for (let i = 0; i < length; i++) {
      const interval = elPositionSet[i]
      const intervalTo = elPositionSet[i + 1]
      if ((!intervalTo || (currentY <= intervalTo)) && (currentY >= interval)) {
        result = i
        diffHeight = intervalTo - currentY
      }
    }
    return { result, diffHeight }
  }

  /**
   * 
   * @param {String} style css
   */
  prefixStyle(style) {
    if (this.vender === false) {
      return false
    }

    if (this.vender === 'standard') {
      return style
    }
    return this.vender + style
  }

  /**
   *  m : ss 
   * @param {Number} time 
   */
  normalizeTime(time) {
    const TIME = time | 0
    const RES_MINUTE = TIME / 60 | 0
    const SECOND = TIME % 60
    let resSecond = SECOND.toString()
    while (resSecond.length < 2) {
      resSecond = '0' + resSecond
    }
    return `${RES_MINUTE}:${resSecond}`
  }

  /**
   * 
   * @param {Array} array
   */
  randomSort(array) {
    const [...ret] = array // 
    const LENGTH = ret.length
    for (let i = 0; i < LENGTH; i++) {
      const j = this.getRandom(0, i)
      const item = ret[j]
      ret[j] = ret[i]
      ret[i] = item
    }
    return ret
  }

  /**
   * [min, max] 
   * @param {Number} min 
   * @param {Number} max 
   */
  getRandom(min, max) {
    return Math.floor(Math.random() * (max - min + 1) + min)
  }

  /**
   * 
   * @param {Function} fun 
   * @param {Number} interval 
   */
  throttle(func, interval) {
    let timer
    return function () {
      if (timer) {
        clearTimeout(timer)
      }

      timer = setTimeout(() => {
        func.apply(this, arguments)
      }, interval)
    }
  }
  // TODO:util.js
}

const elementStyle = document.createElement('div').style
//  css  transform 
function vendor () {
  const transformNames = {
    '-webkit-': '-webkit-transform',
    '-moz-': '-moz-transform',
    '-ms-': '-ms-transform',
    '-o-': '-o-transform',
    standard: 'transform'
  }

  for (const key in transformNames) {
    if (elementStyle[transformNames[key]] !== undefined) {
      return key
    }
  }

  return false
}
